$montserrat: 'Montserrat', sans-serif;
$Merriweather:'Merriweather', serif;


img, picture {
    max-width: 100%;
}
//accordion
html {
    scroll-behavior: smooth;
}

body {
    background: #030027;
    color: #FFFFFF;
    font-family: $montserrat;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 160%;
    z-index: 0;
}
.container__fluid {
max-width: 1820px;
padding: 0 20px;
margin: 0 auto;
}
.container {
    max-width: 1380px;
    padding: 20px;
    margin: 0 auto;
}
.d-flex {
    display: flex;
}
.btn-click {
    background: linear-gradient(94.38deg, #F6921E 45.92%, #D77504 99.96%);     
    font-family: $montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    
    color: #030027;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    height: 54px;
    padding: 0 20px;
    &:hover {
        background: #FFA236;
    }
    &:active {
        background: #D77504;
    }
}
section {
    border-bottom: 0.3px solid rgba(255, 241, 225, 0.1);
    padding: 80px 0;
}
.section__title {
    font-family: $Merriweather;
    font-style: normal;
    font-weight: 300;
    font-size: 40px;
    line-height: 50px;
    color: #FFFFFF;
}




.header {
    border-bottom:0.3px solid rgba(255, 241, 225, 0.2);
    position: sticky;
    top: 0;
    background: #030027;
    z-index: 5;
    &__block {
        height: 85px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .brand {
            font-family: $Merriweather;
            font-style: normal;
            font-weight: 700;
            font-size: 18px;
            line-height: 23px;
            letter-spacing: 0.06em;
            color: #F6921E;
            img, picture {
                display: none;
                width: 100%;
            }
            a {
                height: 100%;
                img {
                    max-width: 65px;
                    width: 100%;
                    height: auto;
                }
            }
        }
        .navbar {
            height: 100%;
            .btn-menu {
                display: none;
                position: relative;
                span {
                    
                }
            }
            .nav-menu {
                height: 100%;
                padding: 0;
                display: flex;
                list-style: none;
                .nav-item {
                    height: 100%;
                   padding: 0 20px;
                   height: 100%;

                   a {
                    height: 100%;
                    display: flex;
                    align-items: center;
                    font-style: normal;
                    font-weight: 300;
                    font-size: 16px;
                    line-height: 20px;
                    color: #EEEEEE;
                    text-decoration: none;
                    width: max-content;
                    position: relative;
                    transition: 0.5s all ease;
                    &::before {
                        width: 0;
                        height: 1px;
                        background: #F6921E;
                        position: absolute;
                        transition: 0.5s all ease;
                        content: "";
                        bottom: -1px;
                        margin: 0 auto;
                        left: -10%;
                        right: -10%;
                    }
                    &:hover {
                        color: #F6921E;
                        &::before {

                            bottom: -1px;
                            width: calc(100% + 20%);
                        }
                    }
                    &:active {
                        color: #D77504;
                    }
                   }
                }
            }
        }
        .btn-click {
            height: 41px;
            color: #000000;
        }
    }
}
.main-section {
    padding: 0;
    border-bottom: 0.3px solid rgba(255, 241, 225, 0.0);
    transition: 4s ease all;
    &__block {
        border: 0.3px solid rgba(255, 241, 225, 0.0);
        border-top: none;
        border-bottom: none;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0 20px;
        height: calc(100vh - 120px);
        transition: 4s ease all;
        img, picture {
            height: auto;
            max-width: 320px;
            opacity: 0;
            transition: all 1.8s ease;
            transform: rotate(-250deg) scale(0.5);
            &.active {
                opacity: 1;
                transform: rotate(-360deg) scale(1);
            }
        }
        span {
            font-style: normal;
            font-weight: 500;
            font-size: 36px;
            line-height: 125%;
            color: #F6921E;
        }
        span.title {
            margin-bottom: 40px;
        }
        span.subtitle {
            margin-top: 40px;
        }
    }
    &.active {
        border-bottom: 0.3px solid rgba(255, 241, 225, 0.1);
        transition: 4s ease all;
        .main-section__block {
            border: 0.3px solid rgba(255, 241, 225, 0.1);
            border-top: none;
            border-bottom: none;
            transition: 4s ease all;
        }
    }
}
.cta {

    &__block {
        padding: 0 20px;
    }
    &__title {
        max-width: 500px;
        width: 100%;
        margin-right: 50px;
    }
    &__box {
        p {
            // font-family: $montserrat;
            margin-bottom: 15px;
            // font-weight: 300;
            // font-size: 16px;
            // line-height: 160%;
            // color: #EEEEEE;
        }
        .btn-click {
            max-width: 216px;
        }
    }
}
.strategy {
    &__block {
        .img {
            max-width: 420px;
            width: 100%;
            margin-right: 130px;
            img, picture {
                width: 100%;
                transition: 0.5s ease all;
                height: auto;
            }
            &:hover {
                img, picture {
                    transform: scale(1.01);
                }
            }
        }
    }
    &__title {
        margin-bottom: 120px;
    }
    &__box {
        .accordion {
            width: 100%;
            max-width: 660px;
            margin-bottom: 25px;
            &__intro {
                position: relative;
                cursor: pointer;
                display: flex;
                justify-content: space-between;
                align-items: center;
                h4 {
                    font-family:$Merriweather;
                    font-style: normal;
                    font-weight: 300;
                    font-size: 24px;
                    line-height: 30px;
                    color: #FFFFFF;
                }
                img, picture{
                    transition: all 0.5s ease-in-out;
                    transform: rotate(0);
                }
            }
            &__content {
                max-height: 0;
                overflow: hidden;
                will-change: max-height;
                transition: all 0.25s ease-out;
                color: #f1f1f1;
                opacity: 0;
            }
        
        
            &__active {
                .accordion__intro {
                    img, picture {
                        transition: all 0.5s ease-in-out;
                        transform: rotate(-180deg);
                    }
                }
        
                .accordion__content {
                    opacity: 1;
                    height: max-content;
                    margin-top: 40px;
                    p {
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }
}
.recent {
    text-align: center;
    padding: 0;
    &__block {
        border: 0.3px solid rgba(255, 241, 225, 0.1);
        border-top: none;
        border-bottom: none;
        padding: 80px 0;
    }
    &__title {
        margin-bottom: 8px;
    }
}
.project {
    &__block {
        justify-content: space-between;
        .img {
            max-width: 420px;
            margin-right: 20px;
            img, picture {
                transition: 0.5s all ease;
            }
            &:hover {
                img, picture {
                    transform: scale(1.01);
                }
            }
        }
    }
    &__box {
        max-width: 700px;
        width: 100%;
        margin: 0 auto;
        
        .ready {
            margin-bottom: 44px;
            h3 {
                font-weight: 500;
                font-size: 16px;
                line-height: 160%;
                color: #EEEEEE;
                margin-right: 40px;
            }
        }
        .projet__title {
            margin-bottom: 24px;
        }
        .result {
            align-items: center;
            justify-content: space-between;
           h4 {
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            color: #FFFFFF;
            margin-bottom: 16px;
            
           }
           span.count {
            font-weight: 500;
            font-size: 40px;
            line-height: 49px;
            color: #FFFFFF;
            margin-bottom: 18px;
            display: flex;
           }
           .progress {
            display: flex;
            align-items: center;
            .take {
                display: flex;
                align-items: center;
                margin-right: 20px;
                img, picture {
                    margin-right: 12px;
                    width: 28px;
                }
                span {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 24px;
                    color: #2CDE5E;
                    
                }
            }
            .prev {
                color: #A3A3A3;
            }
           } 
        }
    }
}
.reviews {
    &__title {
        text-align: center;
        margin-bottom: 80px;
    }
    &__block {
        padding: 0 10px;
        justify-content: space-between;
    }
    &__box {
        margin: 0 10px;
       
        border: 1px solid  rgba(255, 241, 225, 0.2);
        padding: 40px 25px 40px 40px;
        .name {
            display: flex;
            align-items: center;
            margin-bottom: 30px;
            img, picture {
                width: 43px;
                height: 43px;
                margin-right: 10px;
            }
            h3 {
                font-weight: 600;
                font-size: 24px;
                line-height: 28px;
                margin-bottom: 0;
            }
        }
        p {
            margin-bottom: 30px;
        }
        span.date {
            font-size: 14px;
            line-height: 16px;
            font-feature-settings: 'pnum' on, 'lnum' on;
        }
    }
}
.form {
    &__title {
        font-family: $Merriweather;
        max-width: 580px;
        text-align: center;
        font-weight: 400;
        font-size: 32px;
        line-height: 40px;
        margin-bottom: 80px;
    }
    &__block {
        display: flex;
        align-items: center;
        flex-direction: column;
        .form {
            max-width: 380px;
            width: 100%;
            form {
                width: 100%;
                .form-group {
                    position:relative;  
                  
                    & + .form-group {
                    //   margin-top: 30px;
                    }
                }
                label {
                    position: absolute;

                    left: 0;
                    top: 0px;
                    color: #FFFFFF;
                    z-index: 1;
                    font-style: italic;
                    font-weight: 200;
                    transition: transform 150ms ease-out, font-size 150ms ease-out;
                }
                .focused label {
                    transform: translateY(-100%);
                    font-size: .75em;
                  }
                .form-control {
                    border: none;
                    width: 100%;
                    background: none;
                    margin-bottom: 40px;

                    font-weight: 200;
                    font-size: 16px;
                    line-height: 20px;
                    color: #FFFFFF;
                    outline: none;
                    border-bottom:  1px solid #F6921E;
                    padding-bottom: 12px;
                    position: relative;
                }

                input:-webkit-autofill {
                    -webkit-box-shadow: inset 0 0 0 50px #030027; /* цвет вашего фона */
                    -webkit-text-fill-color: #ffffff; /* цвет текста */
                }
                .form-submit {
                    margin: 0 auto;
                    padding: 0 40px;
                    border: none;
                    cursor: pointer;
                }
            }
        }
    }
}

.footer {
    padding: 80px 0 80px;
    &__block {
        max-width: 1200px;
        margin: 0 auto;
        .img {
            float: left;
            margin-right: 100px;
            h3.contact__title{
                font-weight: 500;
                font-size: 24px;
                line-height: 29px;  
                text-align: center;
                color: #FFFFFF;
                margin-bottom: 30px;
            }
            img, picture {
                max-width: 140px;
                height: auto;
            }
        }
        .contacts {
            margin-bottom: 80px;
            &__box {
                margin-right: 120px;
                h4 {
                    font-weight: 300;
                    font-size: 16px;
                    line-height: 160%;
                    color: #F6921E;
                    margin-bottom: 20px;
                    text-transform: uppercase;
                }
                a {
                    font-weight: 300;
                    font-size: 16px;
                    line-height: 160%;
                    color: #FFFFFF;
                    text-decoration: none;

                }
            }
        }
        .social {
            display: flex;
            margin-bottom: 80px;
            a {
                margin-right: 40px;
                text-decoration: none;
                display: flex;
                img, picture {
                    margin-right: 10px;
                    width: 16px;
                    height: 16px;
                }
                span {
                    display: flex;
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 17px;
                    color: #FFFFFF;
                    margin-top: 2px;
                }
            }
        }
        .copy-text {
            img, picture {
                margin-right: 20px;
            }
            span {
                font-size: 12px;
                line-height: 15px;
                letter-spacing: 0.2em;
                color: #FFFFFF;
            }
        }
    }
}
@media (max-width:1150px) {
    .strategy__block .img {
        margin-right: 50px;
    }
    .project__block .img {
        max-width: 350px;
    }
    .section__title, .main-section__block span {
        font-size: 28px;
        line-height: 35px;
    }
    .form__title {
        font-size: 27px;
        line-height: 34px;
    }
    .footer__block .contacts__box {
        margin-right: auto;
    }
}
@media (max-width:990px) {
    .main-section.active .main-section__block {
        border: none;
    }
    .main-section__block, .recent__block {
        border: none;
    }
    .recent__block {
        padding: 0;
    }
    .main-section__block img, .main-section__block picture {
        max-width: 260px;
    }
    .project__block {
        flex-wrap: wrap;
        justify-content: center;
    }
    .project__block .img {
        max-width: 420px;
        margin-bottom: 40px;
    }
    section, .recent, .footer, .main-section__block {
        padding: 40px 0;
    }
    .container__fluid, .container  {
        padding: 0 30p 0 35px;
    }
    .cta__block {
        padding: 0;
    }
    .reviews {
        padding-left: 25px;
        .container {
            padding: 0;
        }
        &__block {
            padding: 0;
            overflow-x: scroll;
            justify-content: start;
            &::-webkit-scrollbar {
                width: 0;
              }
        }
    }
    .reviews__box {
        min-width: 288px;
    }
    .strategy__block .img {
        max-width: 45%;
    }
    .cta__title {
        margin-right: 20px;
    }
    .footer__block .img {
        margin-right: 40px;
    }
    .strategy__block .img {
        margin-right: 30px;
    }
    .strategy__box .accordion__intro h4 {
        font-size: 20px;
        line-height: 25px;
    }
}

@media (max-width:768px) {
    .header {
        border-bottom:none;
        margin-top: 70px;
        position: static;
    }
    .header__block {
        height: 85px;
        padding: 10px 0;
    }
    .header__block .brand {
        height: 100%;
        span {
            display: none;
        }
        img, picture {
            display: block;
            height: 100%;
        }
    }
    .header__block .btn-click, .header__block .navbar .nav-menu {
        display: none;
    }
    .header__block .navbar {
        display: flex;
        align-items: center;
    }
    .header__block .navbar .btn-menu {
        display: flex;
        flex-direction: column;
        position: relative;
        justify-content: space-between;
        width: 30px;
        height: 14px;
        span {
            display: block;

            width: 100%;
            height: 2px;
            background:  #F6921E;
        }
    }
    .main-section__block span {
        font-size: 24px;
        line-height: 29px;
    }
    .cta__block, .strategy__block {
        flex-direction: column;
    }
    .cta__title {
        max-width: 100%;
        margin-bottom: 20px;
    }
    .cta__box .btn-click {
        max-width: 100%;
    }
    .strategy__block .img {
        max-width: 420px;
        margin: 0 auto 40px;
    }
    .strategy__title {
        margin-bottom: 40px;
    }
    .recent__block {
        max-width: 305px;
        margin: 0 auto;
    }
    .form__block .form form .form-submit {
        width: 100%;
    }
    .footer__block .contacts {
        flex-direction: column;
        margin-bottom: 0;

    }
    .footer__block .contacts__box {
        margin-bottom: 40px;
    }
    .footer__block .contacts__box {
        margin-right: 0;
    }
    .footer__block .img {
        margin-right: 34px;
    }
    .footer__block {
        width: max-content;
    }
    .footer__block .img h3.contact__title{
        font-size: 18px;
        line-height: 140%;
        margin-bottom: 10px;
    }
    .footer__block .img img, .footer__block .img picture {
        max-width: 120px;
    }
    .footer__block .contacts__box h4{
        font-size: 14px;
        line-height: 140%;
        margin-bottom: 10px;
    }
    .footer__block .contacts__box a {
        font-size: 14px;
        line-height: 140%;

    }
    .footer__block .social {
        display: flex;
        justify-content: space-between;
        margin-bottom: 40px;
        a {
            margin: 0;
            height: 20px;
            width: 20px;
            img, .picture {
                height: 100%;
                width: auto;
                margin: 0;
            }
            span {
                display: none;
            }
        }
    }
    .copy-text {
        display: flex;
        align-items: center;
    }
    .footer__block .copy-text img, .footer__block .copy-text .picture {
        margin-right: 16px;
    }
    .footer__block .copy-text span {
        letter-spacing: 0;
    }
    .strategy__box .accordion {
        max-width: 100%;
    }
    .reviews__box .name h3 {
        font-size: 20px;
        line-height: 23px;
    }
    .reviews__title {
        max-width: 244px;
        margin: 0 auto 40px;
    }
    .reviews__box {
        padding: 20px;
    }
    .main-section__block {
        height: calc(100vh - 200px);
    }
}
@media (max-width:650px) {
    .project__box .result {
        flex-direction: column;
        justify-content: start;
        align-items: start;
    }
    .project__box .result .box {
        margin-bottom: 26px;
    }
    .project__box .ready {
        margin-bottom: 40px;
        flex-direction: column;
        h3 {
            font-size: 14px;
            line-height: 160%; 
            margin-bottom: 8px;
        }
    }
    .project__box .projet__title {
        margin-bottom: 40px;
    }
}