.burger-content {
  min-height: 100vh;
  height: 100%;
  position: fixed;
  width: 100vw;
  z-index: 100;
  top: 0px;
  left: 0;
  right: 0;
  bottom: 0;
  color: #fff;
  overflow-x: hidden;
  overflow-y: auto;
  display: none;
  &.active {
    display: block;
  }
  // .burger-content__container
  &__container {
    min-height: 100vh;
    padding: 0 30px;
  }
  // .burger-content__head
  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 50px;
    z-index: 102;
  }
  // .burger-content__logo
  &__logo {
    position: relative;
    display: flex;
    width: 65px;
    height: 65px;
    text-align: center;
    & img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: auto;
    }
  }
  // .burger-content__btn-menu-cross
  &__btn-menu-cross {
    background: transparent;
    border: none;
  }
  // .burger-content__icon-cross-menu
  &__icon-cross-menu {
    width: 40px;
    height: 40px;
  }
  &__menu {
    z-index: 102;
    .nav-menu {
      display: flex;
      flex-direction: column;
      list-style: none;
      width: max-content;
      margin-left: auto;
      .nav-item {
        margin-bottom: 30px;
        display: flex;
        justify-content: end;
        margin-left: auto;
        a {
          text-transform: uppercase;
          font-weight: 500;
          font-size: 24px;
          line-height: 29px;
          color: #FFFFFF;
          text-decoration: none;
          text-align: right;
          &:hover {
            font-weight: 600;
            font-size: 28px;
            line-height: 34px;
            text-align: right;
            letter-spacing: 0.2em;
            color: #030027;
          }
        }
      }
    }
  }
  &__footer {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
    margin-top: 50px;
    z-index: 102;
    .social, .contacts {
      display: flex;
      flex-direction: column;
    }
    .contacts {
      &__box {
        margin-bottom: 10px;
        h4 {
          font-weight: 500;
          font-size: 14px;
          line-height: 140%;
          color: #030027;
          margin-bottom: 4px;
          text-transform: uppercase;
        }
        a {
          font-weight: 500;
          font-size: 14px;
          line-height: 140%;
          color: #FFFFFF;
          text-decoration: none;
        }
      }
    }
    .social {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      a {
        height: 20px;
        width: 20px;
        display: flex;
        justify-content: center;
        img {
          height: 100%;
          width: auto;
        }
      }
    }
  }
}

#menu-svg-container {
  display: none;
  transform: unset;
  inset: 0;
  height: 100%;
  width: unset;
  z-index: 99;
  position: fixed;
  &.menu-svg-line-hidden #menu-svg-line {
    opacity: 0;
  }
  &.menu-svg-line-visible #menu-svg-line {
    opacity: 1;
  }
}